.cases{
  padding: 50px 0;
  h2{
    color: #635454;
    text-align: center;
    font-size: 32px;
    margin-bottom: 30px;
    font-weight: 400;
  }
  .container-slider{
    position: relative;
    padding: 0 100px;
    .slides{
      .slide{
        .project{
          width: 500px;
          height: 300px;
          margin: 0 auto;
          position: relative;
          cursor: pointer;
          .img-project{
            position: absolute;
            top:0;
            left:0;
            width: 100%;
            height: 100%;
          }
          .project-info{
            overflow: hidden;
            .info-container{
              display: flex;
              justify-content: space-between;
              align-items: center;
              width: 100%;
              height: 80%;
              .project-name{
                color: #ffffff;
                font-size: 26px;
                line-height: 1;
                span{
                  display: block;
                  font-size: 12px;
                }
              }
              .option-list{
                height: 100%;
                display: flex;
                justify-content: space-around;
                flex-direction: column;
                li{
                  color: #ffffff;
                  padding-left: 20px;
                  position: relative;
                  margin-bottom: 5px;
                  font-size: 12px;
                  &::before{
                    content: '';
                    position: absolute;
                    display: block;
                    width: 18px;
                    height: 18px;
                    background: url("/img/icons/checked.png") 100%/contain no-repeat;
                    left: 0;
                    top: 0;
                  }
                }
              }
            }
            padding: 10px;
            height: 120px;
            width: 100%;
            position: absolute;
            left: 0;
            bottom: -120px;
            background-color: rgba(0, 75, 144, 0.81);
            transition: ease .4s;
            .link{
              width: 80px;
              height: 20px;
              display: block;
              border: 1px solid #fff;
              margin-left: auto;
              color: #ffffff;
              text-decoration: none;
              font-size: 12px;
              text-align: center;
              transition: ease .4s;
              &:hover{
                color: rgb(0, 75, 144);
                background-color: #fff;
              }
            }
          }
          &:hover{
            .project-info{
              bottom: 0;
            }
          }
        }
      }
    }
    .control{
      .btn-prev{
        position: absolute;
        left: 0;
        top: calc(50% - 50px);
        .btn-icon{
          position: relative;
          width: 100px;
          height: 100px;
          cursor: pointer;
          &::before{
            content: '';
            position: absolute;
            display: block;
            width: 80px;
            height: 15px;
            background-color: rgb(191, 191, 191);
            border-radius: 5px;
            transform: rotate(-45deg);
            top:19px;
            right: 0;
          }
          &::after{
            content: '';
            position: absolute;
            display: block;
            width: 80px;
            height: 15px;
            background-color: rgb(191, 191, 191);
            border-radius: 5px;
            transform: rotate(45deg);
            bottom:19px;
            right: 0;
          }
        }
      }
      .btn-next{
        position: absolute;
        right: 0;
        top: calc(50% - 50px);
        .btn-icon {
          position: relative;
          width: 100px;
          height: 100px;
          cursor: pointer;

          &::before {
            content: '';
            position: absolute;
            display: block;
            width: 80px;
            height: 15px;
            background-color: rgb(191, 191, 191);
            border-radius: 5px;
            transform: rotate(45deg);
            top: 19px;
            left: 0;
          }

          &::after {
            content: '';
            position: absolute;
            display: block;
            width: 80px;
            height: 15px;
            background-color: rgb(191, 191, 191);
            border-radius: 5px;
            transform: rotate(-45deg);
            bottom: 19px;
            left: 0;
          }
        }
      }
    }
  }
}
