footer{
  background-color: #307bc1;
  padding-top: 80px;
  padding-bottom: 20px;
  .footer-list{
    margin-bottom: 20px;
    .head-list{
      text-transform: uppercase;
      color: #ffffff;
      font-size: 20px;
      margin-bottom: 5px;
      font-weight: 600;
      text-decoration: none;
      display: block;
      transition:  ease .4s;
      line-height: 1;
      height: 25px;
      &:hover{
        font-size: 22px;
      }
    }
    ul{
      li{
        margin-bottom: 5px;
        a{
          transition: ease .4s;
          color:#ffffff;
          text-decoration: none;
          font-size: 16px;
          height: 25px;
          display: block;
          &:hover{
            font-size: 18px;
          }
        }
      }
    }
  }
  p{
    color: #8c9399;
    text-align: center;
    margin-bottom: 2px;
    line-height: 1.2;
    font-size: 12px;
  }
}

@media screen and (max-width: 1359px){
  footer{
    .footer-list{
      .head-list{
        text-align: center;
      }
      ul{
        li{
          text-align: center;
        }
      }
    }
  }
}
@media screen and (max-width: 767px){
  footer{
    padding-top: 50px;
    .footer-list{
      .head-list{
        font-size: 16px;
        &:hover{
          font-size: 18px;
        }
      }
      ul{
        li{
          margin-bottom: 5px;
        }
      }
    }
  }
}

@media screen and (max-width: 575px){
  footer{
    padding-top: 50px;
    .footer-list{
      .head-list{
        font-size: 18px;
        text-align: center;
      }
      ul{
        li{
          margin-bottom: 5px;
          text-align: center;
          transition: ease .4s;
        }
      }
    }
  }
}
