.order{
  padding: 50px 0 20px;
  background: url('/img/backgrounds/order.png') center no-repeat;
  h2{
    color: #635454;
    text-align: center;
    font-size: 32px;
    margin-bottom: 30px;
    font-weight: 400;
  }
  .container-form{
    form{
      padding: 0 30px;
      .container-input{
        border: 1px solid #9297ab;
        border-radius: 30px;
        margin-bottom: 25px;
        background-color: #ffffff;
        input{
          border: none;
          width: 100%;
          background-color: transparent;
          font-size: 18px;
          padding: 15px 25px;
          line-height: 1;
          color: #9a9a9a;
          &:placeholder-shown{
            opacity: 1;
          }
          &::placeholder{
            opacity: 1;
          }
        }
        &.error{
          border-color: #ff5a58;
          input{
            &::placeholder{
              color: #ff5a58;
            }
            &:placeholder-shown{
              color: #ff5a58;
            }
          }
        }
      }
      textarea{
        margin-bottom: 20px;
        &:placeholder-shown{
          opacity: 1;
        }
        &::placeholder{
          opacity: 1;
        }
        border: 1px solid #9297ab;
        background-color: #ffff;
        border-radius: 30px;
        width: 100%;
        padding: 15px 25px;
        color: #9a9a9a;
        font-size: 18px;
      }
      .container-checkbox{
        display: flex;
        padding-left: 30px;
        margin-bottom: 10px;
        align-items: center;
        label{
          flex-shrink: 0;
          position: relative;
          border: 1px solid #9297ab;
          width: 34px;
          height: 30px;
          border-radius: 10px;
          background-color: #fff;
          margin-right: 20px;
          cursor: pointer;
          &::before{
            content: '';
            position: absolute;
            display: block;
            opacity: 0;
            transition: ease .4s;
            border: 4px solid #635454;
            z-index: 5;
            border-top: none;
            border-left: none;
            width: 12px;
            height: 20px;
            transform: rotate(45deg);
            left: 10px;
            top: 2px;

          }
          input{
            width: 0;
            height: 0;
            position: absolute;
            background-color: transparent;
            border: none;
            z-index: 0;
            opacity: 0;
          }
        }
        .checkbox-text{
          margin-top: -10px;
          align-self: center;
          color: #635454;
          font-size: 20px;
          line-height: 1;
        }
        &.checked{
          label{
            &::before{
              opacity: 1;
            }
          }
        }
      }
      input[type="submit"]{
        width: 100%;
        background: url("/img/backgrounds/btn-bg-2.png") 100% no-repeat;
        background-size: cover;
        color: #ffffff;
        font-size: 28px;
        text-align: center;
        border:none;
        border-radius: 30px;
        height: 60px;
        letter-spacing: 2px;
        margin: 10px 0;
        box-shadow: 3px 3px 4px rgba(0, 0, 0, 0.51);
        &:hover{
          background: url("/img/backgrounds/btn-bg-1.png") 100% no-repeat;
          background-size: cover;
        }
      }
    }
  }
}
@media screen and (max-width: 575px){
  .order{
    .container-form{
      form{
        padding: 0;
      }
    }
  }
}
