
  #mobile-menu{
    padding: 50px 20px;
    position: relative;
    background: url('/img/backgrounds/pr.png') 100% no-repeat;
    background-size: cover;
    width: 360px;
    &:after{
      content: '';
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background-color: rgba(75,159,203,.88);
    }
    .btn-close{
      position: absolute;
      top:10px;
      right: 10px;
      z-index: 1006;
      .icon-close{
        width: 20px;
        height: 20px;
        position: relative;
        cursor: pointer;
        .close-block{
          height: 2px;
          width: 28px;
          background-color: #fff;
          position: absolute;
          transition: ease .4s;
          &:nth-child(1){
            left:-4px;
            top:10px;
            transform: rotate(45deg);
          }
          &:nth-child(2){
            right: -4px;
            top:10px;
            transform: rotate(-45deg);
          }
        }
        &:hover{
          .close-block{
            background-color: #5e5556;
          }
        }
      }
    }
    .phone{
      position: relative;
      font-size: 16px;
      text-transform: uppercase;
      color: #fff;
      font-weight: 600;
      text-decoration: none;
      margin-bottom: 20px;
      display: block;
      z-index: 2;
      transition: ease .4s;
      &:hover{
        color: #5e5556;
      }
    }
    .lang{
      margin: 0 -10px;
      display: flex;
      position: relative;
      z-index: 2;
      .link-lang{
        margin: 0 10px;
        font-size: 16px;
        text-transform: uppercase;
        color: #ffff;
        font-weight: 600;
        text-decoration: none;
        margin-bottom: 20px;
        transition: ease .4s;
        &:hover{
          color: #5e5556;
        }
      }
    }
    ul{
      position: relative;
      z-index: 2;
      li{
        margin-bottom: 10px;
        a{
          font-size: 18px;
          text-transform: uppercase;
          color: #ffff;
          font-weight: 600;
          text-decoration: none;
          line-height: 20px;
          transition: ease .4s;
          &:hover{
            font-size: 20px;
          }
        }
      }
    }
  }
