header{
  padding: 50px 0 170px;
  background: url("/img/backgrounds/header-bg.png")  no-repeat;
  .logo{
    margin: 30px 150px 0 50px;
    img{
      display: block;
    }
  }
  .phone{
    color: #5e5556;
    text-decoration: none;
    font-weight: 600;
    font-size: 18px;
  }
  .lang{
    display: flex;
    margin: 0 -10px;
    .link-lang{
      color: #5e5556;
      text-decoration: none;
      font-weight: 600;
      padding: 0 10px;
      font-size: 18px;
      transition: ease .4s;
      &:hover{
        color: #4360af;
      }
    }
  }
  .nav-top{
    ul{
      display: flex;
      li{
        margin: 0 15px;
        a{
          text-transform: uppercase;
          color: #796968;
          text-decoration: none;
          font-size: 20px;
          letter-spacing: 1px;
          transition: ease .4s;
          line-height: 1;
          min-height: 22px;
          &:hover{
            color: #190c0c;
          }
        }
      }
    }
  }
  .nav-bottom{
    ul{
      display: flex;
      justify-content: space-between;
      li{
        a{
          text-transform: uppercase;
          color: #4360af;
          text-decoration: none;
          font-size: 22px;
          font-weight: 600;
          letter-spacing: 1px;
          transition: ease .4s;
          &:hover{
            color: #48abff;
          }
        }
      }
    }
  }
  .mr-8{
    margin-right: 8rem;
  }
  h1{
    color: #fff;
    text-shadow: 2px 2px #1b558f;
    font-weight: 400;
    letter-spacing: 2px;
    font-size: 40px;
    line-height: 50px;
    margin-bottom: 30px;
  }
  p{
    font-size: 26px;
    color: #ffffff;
    letter-spacing: 1px;
    margin-bottom: 0px;
  }
  .btn-header{
    display: block;
    width: 350px;
    border: 2px solid #4780c4;
    border-radius: 30px;
    text-decoration: none;
    background-color: rgba(240, 238, 238, 0.49);
    text-align: center;
    color: #4780c4;
    transition: ease .4s;
    font-size: 26px;
    padding: 10px 10px 12px;
    margin-top: 45px;
    &:hover{
      background-color: #ffffff;
    }

  }
}

@media screen and (max-width: 1650px){
  header{
    .nav-bottom{
      ul{
        li{
          a{
            font-size: 18px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1499px){
  header{
    .logo{
      margin: 30px 10px 0 10px;
      img{
        display: block;
      }
    }
  }
}

@media screen and (max-width: 1359px){
  header{
    .btn-header{
      width: 100%;
      font-size: 22px;
    }
    .mr-8{
      margin-right: 0rem;
    }
    .phone{
      font-size: 16px;
    }
    .lang{
      .link-lang{
        font-size: 16px;
      }
    }
    .nav-top{
      ul{
        li{
          a{
            font-size: 16px;
          }
        }
      }
    }
    .nav-bottom{
      ul{
        li{
          a{
            font-size: 16px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1100px){
  header{
    .nav-bottom{
      ul{
        flex-wrap: wrap;
        li{
          width: 50%;
          a{
            width: 100%;
            text-align: center;
            display: block;
          }
        }
      }
    }
  }
}
@media screen and (max-width: 991px){
  header{
    padding: 20px 0 170px 0;
    h1{
      margin-bottom: 10px;
      font-size: 32px;
      text-align: center;
      line-height: 32px;
      br{
        display: none;
      }
    }
    p{
      font-size: 18px;
      text-align: center;
    }
    .btn-header{
      width: 60%;
      margin: 10px auto 0;
    }

    .logo{
      margin: 0;
    }
    .mobile-menu-link{
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      height: 23px;
      .icon-block{
        width: 31px;
        height: 3px;
        background-color: #5e5556;
        border-radius: 3px;
      }
    }
  }
}
@media screen and (max-width: 575px){
  header{
    .btn-header{
      width: 100%;
      margin: 10px auto 0;
    }
    h1{
      font-size: 22px;
      line-height: 22px;
    }
  }

}
