.text-1{
  padding: 30px 0;
}
.text-2{
  padding: 85px 0 50px;
}
.text-3{
  padding: 100px 0 150px;
}
.text-1,.text-2,.text-3{
  p{
    color: #635454;
    margin-bottom: 15px;
    font-size: 18px;
    padding-right: 20px;
    line-height: 1.2;
  }
}
@media screen and (max-width: 991px){
  .text-1,.text-2,.text-3{
    p{
      padding-right: 0px;
    }
  }
}
@media screen and (max-width: 575px){
  .text-3,.text-2,.text-1{
    padding: 20px 0 30px;
  }
}
