.services{
  margin-top: -150px;
  .container-service{
    overflow: hidden;
    position: relative;
    color: #ffffff;
    height: 100%;
    padding: 1.82vw;
    display: flex;
    flex-direction: column;
    .service-img{
      position: relative;
      z-index: 5;
      display: flex;
      justify-content: center;
      height: 64px;
      margin-bottom: 2.5vw;
      transition: ease-in-out .5s;
      img{
        max-width: 87px;
        max-height: 77px;
      }
    }
    .service-name{
      position: relative;
      font-size: 1.6vw;
      font-weight: 400;
      text-transform: uppercase;
      z-index: 5;
      text-align: center;
      margin-bottom: 4.5vw;
    }
    .btn-container{
      position: relative;
      width: 90%;
      margin-top: auto;
      height: 3.5vw;
      align-self: center;
    }
    .btn-transparent{
      position: absolute;
      width: 100%;
      display: block;
      border: 3px solid #fff;
      border-radius:35px;
      font-size: 1.5vw;
      text-align: center;
      line-height: 1;
      color: #ffffff;
      text-decoration: none;
      transition: ease .6s;
      opacity: 0;
      z-index: 1;
      bottom: -100px;
      .container-bg{
        border-radius:35px;
        width: calc(100% + 6px);
        height: 100%;
        margin: 0 -3px;
        padding: .8vw 15px 1vw;
        position: relative;
        z-index: 2;
        overflow: hidden;
        &::before{
          content: '';
          position: absolute;
          display: block;
          width: 60%;
          height: 100%;
          top:0;
          bottom: 0;
          left: 20%;
          right: 20%;
          background-color: transparent;
          transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
          transition: .3s;
          z-index: -1;
        }
      }
      &:hover{
        .container-bg {
          &::before {
            left:0px;
            right: 0px;
            width: 100%;
            background-color: #fff;
          }
        }
      }
    }
    .service-work-list{
      position: relative;
      z-index: 5;
      margin-bottom: 2.5vw;
      transition: ease-in-out .5s;
      li{
        padding-left: 35px;
        position: relative;
        margin-bottom: 1vw;
        transition: ease .4s;
        a{
          font-size: 1.41vw;
          line-height: 1;
          cursor: pointer;
          color: #ffffff;
          text-decoration: none;
        }
        &:hover{
          transform: scale(1.1);
        }
        &::before{
          content: '';
          position: absolute;
          width: 26px;
          height: 23px;
          display: block;
          left: 0;
          top: 4px;
          background: url('/img/icons/checked.png') 100%/cover no-repeat;

        }
      }
    }
    &.develop{
      transition: ease .4s;
      .btn-transparent{
        &:hover{
          color: rgb(0, 81, 156);
        }
      }
      &::before{
        content: '';
        position: absolute;
        display: block;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 81, 156, 0.88);
        z-index: 2;
        left: 0;
        top:0;
        transition: ease-out 1s;
      }
      &::after{
        content: '';
        position: absolute;
        display: block;
        left: 0;
        right: 0;
        top:0;
        bottom: 0;
        background: url("/img/backgrounds/develop.png") 100%/cover no-repeat;
        z-index: 1;
        transition: ease-out 1s;
      }
    }
    &.support{
      .btn-transparent{
        &:hover{
          color: rgb(200, 52, 47);
        }
      }
      &::before{
        content: '';
        position: absolute;
        display: block;
        width: 100%;
        height: 100%;
        background-color: rgba(200, 52, 47, 0.88);
        z-index: 2;
        left: 0;
        top:0;
        transition: ease-out 1s;
      }
      &::after{
        content: '';
        position: absolute;
        display: block;
        left: 0;
        right: 0;
        top:0;
        bottom: 0;
        background: url("/img/backgrounds/support.png") 100%/cover no-repeat;
        z-index: 1;
        transition: ease-out 1s;
      }
    }
    &.testing{
      .btn-transparent{
        &:hover{
          color: rgb(158, 48, 117);
        }
      }
      &::before{
        content: '';
        position: absolute;
        display: block;
        width: 100%;
        height: 100%;
        background-color: rgba(158, 48, 117, 0.88);
        z-index: 2;
        left: 0;
        top:0;
        transition: ease-out 1s;
      }
      &::after{
        content: '';
        position: absolute;
        display: block;
        left: 0;
        right: 0;
        top:0;
        bottom: 0;
        background: url("/img/backgrounds/testing.png") 100%/cover no-repeat;
        z-index: 1;
        transition: ease-out 1s;
      }
    }
    &.pr{
      .btn-transparent{
        &:hover{
          color: rgb(75, 159, 203);
        }
      }
      &::before{
        content: '';
        position: absolute;
        display: block;
        width: 100%;
        height: 100%;
        background-color: rgba(75, 159, 203, 0.88);
        z-index: 2;
        left: 0;
        top:0;
        transition: ease-out 1s;
      }
      &::after{
        content: '';
        position: absolute;
        display: block;
        left: 0;
        right: 0;
        top:0;
        bottom: 0;
        background: url("/img/backgrounds/pr.png") 100%/cover no-repeat;
        z-index: 1;
        transition: ease-out 1s;
      }
    }
    &:hover{
      .service-img{
        margin-top: -6.5vw;
        opacity: 0;
      }
      .service-work-list{
        margin-bottom: 9vw;
      }
      &.develop{
        &::before{
          background-color: rgb(0, 81, 156);
        }
        &::after{
          left: -25%;
          right: -25%;
          top:-25%;
          bottom: -25%;
          width: unset;
          height: unset;
        }
      }
      &.support{
        &::before{
          background-color: rgb(200, 52, 47);
        }
        &::after{
          background: url("/img/backgrounds/support.png") 100%/cover no-repeat;
          left: -25%;
          right: -25%;
          top:-25%;
          bottom: -25%;
          width: unset;
          height: unset;
        }
      }
      &.testing{
        &::before{
          background-color: rgb(158, 48, 117);
        }
        &::after{
          background: url("/img/backgrounds/testing.png") 100%/cover no-repeat;
          left: -25%;
          right: -25%;
          top:-25%;
          bottom: -25%;
          width: unset;
          height: unset;
        }
      }
      &.pr{
        &::before{
          background-color: rgb(75, 159, 203);
        }
        &::after{
          background: url("/img/backgrounds/pr.png") 100%/cover no-repeat;
          left: -25%;
          right: -25%;
          top:-25%;
          bottom: -25%;
          width: unset;
          height: unset;
        }
      }
      .btn-transparent{
        opacity: 1;
        z-index: 5;
        bottom: 0;
      }
    }
  }
}
