.why-us{
  position: relative;
  z-index: 5;
  padding: 30px 0 0;
  background-color: #5cbdf0;
  h2{
    text-align: center;
    color: #ffffff;
    font-size: 32px;
    margin-bottom: 30px;
    font-weight: 400;
  }
  .text-web{
    font-size: 30px;
    text-align: center;
    color: #ffffff;
    margin-top: -80px;
    margin-left: -14px;
  }
  .container-reasons{
    .container-reason{
      margin-bottom: 60px;
      .img-reason{
        width: 75px;
        height: 65px;
        flex-shrink: 0;
        display: flex;
        justify-content: center;
        align-items: flex-start;
        padding-top: 5px;
      }
      .text-reason{
          color:#fff;
        &.ls-2{
          letter-spacing: 2px;
        }
        .name-reason{
          text-transform: uppercase;
          font-weight: 600;
          font-size: 18px;
          line-height: 1;
        }
        .desc-reason{
          font-size: 18px;
          line-height: 1.2;
        }
      }
    }
    &.left{
      .container-reason{
        display: flex;
        .text-reason{
          padding-left: 10px;
          margin-right: 10px;
          text-align: right;
          &.pl-100{
            padding-left: 100px;
          }
          &.pl-10{
            padding-left: 10px;
          }
        }
        position: relative;
        &::before{
          content: '';
          position: absolute;
          width: 33px;
          height: 2px;
          display: block;
          background-color: #fff;
        }
        &.top{
          &::before{
            transform: rotate(45deg);
            right: -90px;
            top: 100px;
          }
        }
        &.middle{
          &::before{
            right: -60px;
            top: 34px;
          }
        }
        &.bottom{
          margin-bottom: 20px;
          &::before{
            transform: rotate(-45deg);
            right: -90px;
            top: -55px;
          }

        }
      }

    }
    &.right{
      .container-reason{
        position: relative;
        &.mb-80{
          margin-bottom: 80px;
        }
        margin-left: -14px;
        display: flex;
        .text-reason{
          margin-left: 10px;
          order:1;
          text-align: left;
        }
        &::before{
          content: '';
          position: absolute;
          width: 33px;
          height: 2px;
          display: block;
          background-color: #fff;
        }
        &.top{
          &::before{
            transform: rotate(-45deg);
            left: -90px;
            top: 100px;
          }
        }
        &.middle{
          &::before{
            left: -60px;
            top: 34px;
          }
        }
        &.bottom{
          margin-bottom: 20px;
          &::before{
            transform: rotate(45deg);
            left: -90px;
            top: -55px;
          }

        }
      }
    }

  }
  &::before{
    content: '';
    position: absolute;
    display: block;
    bottom: -150px;
    border: 650px solid transparent;
    border-top: 80px solid #5cbdf0;
    left: calc((100% - 1300px)/2);
    border-bottom: 80px solid transparent;
  }
}

@media screen and (max-width: 1359px){

  .why-us{
    &::before{
      bottom: -80px;
      border: 480px solid transparent;
      left: calc((100% - 960px)/2);
      border-top: 40px solid #5cbdf0;
      border-bottom: 40px solid transparent;
    }
    .container-reasons{
      &.left{
        .container-reason{
          &.mb-77{
            margin-bottom: 77px;
          }
          &.mb-63{
            margin-bottom: 63px;
          }
          .img-reason{

          }
          .text-reason{
            text-align: left;
            order: 1;
            margin-left: 10px;
            margin-right: 0;
            &.pl-100{
              padding-right: 100px;
              padding-left: 0;
            }
            &.pl-10{
              padding: 0;
            }
          }
          &::before{
            display: none;
          }
        }
      }
      &.right{
        .container-reason{
          &::before{
            display: none;
          }
        }
      }
    }
  }
}
@media screen and (max-width: 991px){
  .why-us{
    &::before{
      bottom: -60px;
      border: 360px solid transparent;
      left: calc((100% - 720px)/2);
      border-top: 30px solid #5cbdf0;
      border-bottom: 30px solid transparent;
    }
    .container-reasons{
      .container-reason{
        margin-bottom: 30px;
      }
      &.left,&.right{
        .container-reason{
          margin-left: 0;
          &.mb-80, &.mb-63{
            margin-bottom: 30px;
          }
          .img-reason{
            width: 40px;
            height: 35px;
          }
          .text-reason{
            &.ls-2{
              letter-spacing: unset;
            }
            &.pl-100,&.pl-10{
              padding: 0;
            }
          }
        }
      }
    }
  }
}
@media screen and (max-width: 767px){
  .why-us {
    &::before {
      bottom: -40px;
      border: 270px solid transparent;
      left: calc((100% - 540px) / 2);
      border-top: 20px solid #5cbdf0;
      border-bottom: 20px solid transparent;
    }
  }
}
@media screen and (max-width: 575px){
  .why-us {
    padding: 30px 0;
    &::before {
      display: none;
    }
  }
}
